

.isl-navbar-switch1 {

    background: transparent linear-gradient(118deg, #E8F6FF 0%, #ECF2DC 100%) 0% 0% no-repeat padding-box;
    padding: rem(15px) 0;
    border-bottom: 1px solid #e5e5e5;

    img {
        max-width: 100%;
    }

    &__top-bar {
        margin-bottom: rem(15px);

        ul {
            display: flex;
            justify-content: end;
            margin-bottom: 0;
            &>li {
                list-style: none;
                background: rgba(255, 255, 255, 0.6);
                border-radius: 10px;
                padding: rem(7px) rem(15px);

                p {
                    margin-bottom: 0;
                    font-size: rem(15px);
                    color: $black;
                }
                &:not(:last-child) {
                   margin-right: rem(30px);
                }
                a {
                       text-decoration: none;
                   }
            }
        }
    }

    &__nav {
        background: $white;
        border-radius: 10px;
        width: 100%;
        height: rem(80px);
        align-content: center;
        margin-bottom: rem(40px);
        box-shadow: 0px 1px 2px #00000029;

        &--inner {
            display: flex;
            justify-content: end;
            align-items: center;
            @include col-side-paddings;
        }

        &--logo {
            margin-right: auto;

            img {
                height: rem(42px);
                width: auto;
            }
        }

        &--items {
            display: flex;
            margin-bottom: 0;

            ul {
                margin-bottom: 0;

                li {
                    list-style: none;
                    display: inline-flex;
                    a {

                        font-size: rem(18px);
                        font-weight: 500;
                        margin-right: 2rem;
                        height: rem(80px);
                        line-height: rem(80px);
                        color: $font-main-color;
                        padding: 0 1rem;
                        transition: 0.3s;
                        text-decoration: none;

                        @include media-breakpoint-down(lg) {
                            margin-right: 0.5rem;
                        }

                        &:hover {
                            background: $primary;
                            color: $white;
                        }
                    }
                }
            }

        }

        &--mobile-menu {
            div {
                width: rem(35px);
                height: rem(3px);
                background-color: black;
                margin: rem(6px) 0;
            }
        }
    }

    &__hero {
        @include element-to-element-margin-medium;

        &--row {
            display: flex;
            flex-wrap: wrap;
            align-items: center;
        }

        &--col {
            flex-basis: 50%;
            @include col-side-paddings;

            &-text {
                text-align: left;
            }

            &-image {
                display: block;
            }
        }
    }

    &.mobile {

            .isl-navbar-switch1__top-bar {
                justify-content: unset;

                &--item {
                    &:last-child {
                        margin-left: auto;
                    }
                }
            }




            .isl-navbar-switch1__nav {
                height: auto;
                padding: rem(15px) rem(20px);

                &--inner {
                    justify-content: unset;
                    padding-left: unset;
                    padding-right: unset;
                }

                &--items {
                    display: none;
                }

                &--logo {
                    img {
                        height: auto;
                        width: 12rem;
                    }
                }
            }



    }

}
